* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  background: radial-gradient(
    circle at 30% 30%,
    #2a4d7a 0%,
    #1b3662 50%,
    #0d1b2a 100%
  );
  background-attachment: fixed;
  background-size: cover;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  position: relative;
  overflow-x: hidden;
}

body::before {
  content: "";
  position: absolute;
  inset: 0;
  background: inherit;
  filter: blur(40px);
  z-index: -1;
}
.features-section {
  padding: 80px 20px;
  width: 100%;
}

.features-section .content-wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  color: white;
  padding: 60px 20px;
  max-width: 1100px;
  margin: 0 auto;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  text-align: left;
  color: white;
  margin-bottom: 40px;
}

.section-divider {
  border: none;
  height: 2px;
  background-color: white;
  width: 100%;
  margin: 20px 0 40px 0;
}

.section h1,
.section h2,
.section h3,
.section p,
.section strong {
  color: white;
}

.content-wrapper {
  margin-bottom: 80px; /* spacing between sections */
}

.content-wrapper {
  text-align: left;
}
